import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Row, Card, Form, Divider, Checkbox, message, Col, Spin } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { DatePickerString, SelectAjax, SelectMultiAjax, InputOnlyNumber } from 'components/common';
import { getYearEducations } from 'services/yearEducationService';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getCourses } from 'services/course';
import { getStudyFields } from 'services/StudyField';
import { getClasss } from 'services/class';
import {
  getDataForSelect,
  getRegisterConfigById,
  addRegisterConfig,
  editRegisterConfig,
} from 'services/registerConfig';
import { getSemesters } from 'services/semesterService';
import { BtnSave, BtnCancel } from 'components/common/button';
import { getQueryParamId } from 'utils/urlUtil';

const initialValueDefault = {
  id: '',
  trainTypeId: '',
  facultyId: '',
  courseId: '',
  registerSessionId: '',
  semesterId: '',
  yearEduId: '',
  minCreditNormal: '',
  maxCreditNormal: '',
  replayCreditNormal: '',
  isCheckMinNormal: false,
  isCheckMaxNormal: false,
  minCreditLow: '',
  maxCreditLow: '',
  replayCreditLow: '',
  isCheckMinLow: false,
  isCheckMaxLow: false,
  startDate: '',
  endDate: '',
  startDate2: '',
  endDate2: '',
};

@Form.create({ name: 'formSpcializeTraining' })
class FormComeFrom extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: null,
      loadingForm: false,
    };
    this.updateTitle = 'Cp nht quy nh';
    this.addNewTitle = 'Thm quy nh';
  }

  componentDidMount = async () => {
    const { form } = this.props;
    if (getQueryParamId()) {
      this.loadDataForm();
    } else {
      form.setFieldsValue(initialValueDefault);
    }
  };

  loadDataForm = async () => {
    const { form } = this.props;
    this.setState({ loadingForm: true });
    const ComeFromByID = await getRegisterConfigById(getQueryParamId());
    this.setState({ loadingForm: false });
    const { classIds } = ComeFromByID.data;
    const classString = classIds ? classIds.split(',') : [];
    const classFormat = classString.map(item => parseInt(item));
    form.setFieldsValue({
      ...ComeFromByID.data,
      classIds: classFormat,
    });
    this.setState({ id: getQueryParamId() });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (this.state.id) {
          editRegisterConfig(this.state.id, values).then(() => {
            message.success('Cp nht thnh cng.');
            this.props.history.goBack();
          });
        } else {
          addRegisterConfig(values).then(() => {
            message.success('To thnh cng.');
            this.props.history.goBack();
          });
        }
      } else {
        console.log('err form', err);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;

    const { id: specializeTrainingId, loadingForm } = this.state;
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    const trainTypeId = getFieldValue('trainTypeId');
    return (
      <Card title={specializeTrainingId ? this.updateTitle : this.addNewTitle}>
        <Spin spinning={loadingForm} size="large">
          <Form onSubmit={this.handleSubmit} className="login-form">
            <Row>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }} hasFeedback>
                <Form.Item label="H">
                  {getFieldDecorator('trainTypeId', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<SelectAjax service={getTrainTypes} />)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }} hasFeedback>
                <Form.Item label="Khoa">
                  {getFieldDecorator('facultyId', {
                    // rules: [
                    //   {
                    //     required: true,
                    //     message: 'Vui lng nhp thng tin!',
                    //   },
                    // ],
                  })(<SelectAjax service={getFacultys} />)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }} hasFeedback>
                <Form.Item label="Kha">
                  {getFieldDecorator('courseId', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<SelectAjax service={getCourses} />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col xs={24} sm={12} md={12} style={{ paddingRight: 5 }} hasFeedback>
                <Form.Item label="CN">
                  {getFieldDecorator('studyFieldId', {
                    // rules: [
                    //   {
                    //     required: true,
                    //     message: 'Vui lng nhp thng tin!',
                    //   },
                    // ],
                  })(<SelectAjax service={getStudyFields} params={{ trainTypeId }} />)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={12} style={{ paddingRight: 5 }} hasFeedback>
                <Form.Item label="Lp">
                  {getFieldDecorator('classIds', {
                    // rules: [
                    //   {
                    //     required: true,
                    //     message: 'Vui lng nhp thng tin!',
                    //   },
                    // ],
                  })(<SelectMultiAjax service={getClasss} />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }}>
                <Form.Item label="Nm hc" hasFeedback>
                  {getFieldDecorator('yearEduId', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(
                    <SelectAjax
                      service={getYearEducations}
                      setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                      typeValue="int"
                    />
                  )}
                </Form.Item>
              </Col>

              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }}>
                <Form.Item label="Hc k">
                  {getFieldDecorator('semesterId', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(
                    <SelectAjax
                      setFieldsValue={setFieldsValue}
                      service={getSemesters}
                      params={{ yearEduId }}
                      allowGetData={!!yearEduId}
                      typeValue="int"
                    />
                  )}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }}>
                <Form.Item label="t ng k" hasFeedback>
                  {getFieldDecorator('registerSessionId', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(
                    <SelectAjax
                      service={getDataForSelect}
                      allowGetData={!!trainTypeId}
                      params={{ trainTypeId, semesterId, yearEduId }}
                      keyLabel="label"
                      keyValue="value"
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Divider orientation="left">
              S tn ch ng k i vi sinh vin xp hng bnh thng
            </Divider>
            <Row>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }}>
                <Form.Item label="Ti thiu:" hasFeedback>
                  {getFieldDecorator('minCreditNormal', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<InputOnlyNumber />)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }}>
                <Form.Item label="Ti a:" hasFeedback>
                  {getFieldDecorator('maxCreditNormal', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<InputOnlyNumber />)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }}>
                <Form.Item label="S tn ch ng k hc li:" hasFeedback>
                  {getFieldDecorator('replayCreditNormal', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<InputOnlyNumber />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col xs={24} sm={12} md={12}>
                <Form.Item>
                  {getFieldDecorator('isCheckMinNormal', {
                    valuePropName: 'checked',
                  })(<Checkbox>Kim tra s tn ch ti thiu khi sinh vin ng k</Checkbox>)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={12}>
                <Form.Item>
                  {getFieldDecorator('isCheckMaxNormal', {
                    valuePropName: 'checked',
                  })(<Checkbox>Kim tra s tn ch ti a khi sinh vin ng k</Checkbox>)}
                </Form.Item>
              </Col>
            </Row>
            <Divider orientation="left">S tn ch ng k i vi sinh vin xp hng yu</Divider>
            <Row>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }}>
                <Form.Item label="Ti thiu:" hasFeedback>
                  {getFieldDecorator('minCreditLow', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<InputOnlyNumber />)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }}>
                <Form.Item label="Ti a:" hasFeedback>
                  {getFieldDecorator('maxCreditLow', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<InputOnlyNumber />)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }}>
                <Form.Item label="S tn ch ng k hc li:" hasFeedback>
                  {getFieldDecorator('replayCreditLow', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<InputOnlyNumber />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col xs={24} sm={12} md={12}>
                <Form.Item label="">
                  {getFieldDecorator('isCheckMinLow', {
                    valuePropName: 'checked',
                  })(<Checkbox>Kim tra s tn ch ti thiu khi sinh vin ng k</Checkbox>)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={12}>
                <Form.Item label="">
                  {getFieldDecorator('isCheckMaxLow', {
                    valuePropName: 'checked',
                  })(<Checkbox> Kim tra s tn ch ti a khi sinh vin ng k</Checkbox>)}
                </Form.Item>
              </Col>
            </Row>
            <Divider orientation="left">Ngy ng k ln 1</Divider>
            <Row>
              <Col xs={24} sm={12} md={12}>
                <Form.Item label="T ngy:" hasFeedback>
                  {getFieldDecorator('startDate', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<DatePickerString placeholder="Chn ngy" />)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={12}>
                <Form.Item label="n ngy:" hasFeedback>
                  {getFieldDecorator('endDate', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<DatePickerString placeholder="Chn ngy" />)}
                </Form.Item>
              </Col>
            </Row>
            <Divider orientation="left">Ngy ng k ln 2 (nu c)</Divider>
            <Row>
              <Col xs={24} sm={12} md={12}>
                <Form.Item label="T ngy:" hasFeedback>
                  {getFieldDecorator(
                    'startDate2',
                    {}
                  )(<DatePickerString placeholder="Chn ngy" />)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={8} style={{ paddingRight: 5 }}>
                <Form.Item label="n ngy:" hasFeedback>
                  {getFieldDecorator('endDate2', {})(<DatePickerString placeholder="Chn ngy" />)}
                </Form.Item>
              </Col>
            </Row>
            <Form.Item label="">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormComeFrom {...props} />
  </Page>
));



